@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700;800;900&family=Roboto:wght@100;300;400;500;900&display=swap");
  

  
  body {
    padding: 0;
    background: var(--color-pink);
  }
  
  h2 {
    color: var(--color-gray);
    font-size: var(--text-size-large);
  }
  .card-span {
    font-weight:bolder;
    font-size: var(--text-size-small);
    font-family: "Montserrat";
  }
  .card-item {
    color: var(--color-gray);
    font-family: "Montserrat";
    line-height: 200%;
    text-align: start;
  }
  .card-word {
    background-color: var(--color-white);
    width: 1000px;
    display: grid;
    grid-template-columns: 3fr 2fr 5.5fr 1.5fr 3fr;
    grid-template-rows: 1fr;
    margin: 5px;
    border-radius: 10px;
    padding: 0px 30px;
    justify-content: left;
    gap: 20px;
  }
  .card-buttons {
    display: flex;
    flex-direction: row;
    gap: 10px;
    justify-content: flex-end;
    align-items: center;
    
  }
  

  .selected {
    background-color:rgb(253, 222, 222);
  }

  .cardSaveButton {
    height:  var(--min-tap-target-height);
    width:  var(--min-tap-target-width);
    /* background-color:#66dbba; */
    border: none;
    border-radius: 5px;
    /* color: var(--color-white); */
    /* font-weight:800; */
    /* font-size: 19px; */
    cursor: pointer;
  }
.cardSaveButton:hover{
  background-color:#8ee1c9;
